<template>
    <div class="content">
        <div class="startPage" v-if="AnswerStatus=='start'">
            <div class="question-title">
                <h2>本问卷共包含12道题目</h2> 
            </div>
            <div class="startPage-content">
                风险提示： <br>
                基金投资需承担各类风险，本金可能遭受失。<br>
                同时，还要考虑市场风险、信用风险、流动风险、操作风险等各类投资风险。<br>
                您在基金认购过程中应当注意核对自己的风险识别和风险承受能力，选择与自己风险识别能力和风险承受能力相匹配的私募基金。 
            </div>
            <div class="startPage-btn">
                <el-button type="primary" @click="AnswerStatus='select'" >开始答题</el-button>
            </div>
        </div>
        <div class="quetion-view" v-if="AnswerStatus=='select'">
            <div class="question-content">
                {{nowQuestion.id}} 、{{nowQuestion.content}}
            </div>
            <div class="question-select-items">
                <el-radio-group v-model="nowQuestion.optionValue">
                    <div class="question-select-item" v-for="(item,index) of nowQuestion.selectOption " :key="index">
                        <el-radio :label='index+1'>{{item}}</el-radio>
                    </div>
                </el-radio-group>
            </div>
            <div class="question-select-btn-box ">
                <el-button type="primary" class="left" @click="getOtherQuestion(-1)" :disabled='nowQuestion.id==listArr[0].id'>上一题</el-button>
                <el-button type="primary" class="right" @click="getOtherQuestion(1)" v-if="nowQuestion.id!=listArr[listArr.length-1].id">下一题</el-button>
                <el-button type="primary" class="right" @click="commitAnswer()" v-else>提交</el-button>
            </div>
        </div>
        <div class="quetion-success" v-if="AnswerStatus=='success'">
            <div>提交成功</div>
            <div>答卷人：张三丰</div>
            <div>答卷时间：2018年5月23日 12:00</div>
        </div>
    </div>
</template>

    <script>
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from '../../config/mUtils'
    export default {
        data() {
            return {
                // optionValue:null,
                listArr: [{
                    content: '您的主要收入来源是?',
                    selectOption: [
                        '工资、劳务报酬 ', '生产经营所得 ', '利息、股息、转让等金融性资产收入 ', '出租、出售房地产等非金融性资产收入 ', '无固定收入 '
                    ],
                    optionValue: '',
                }, {
                    content: '您的家庭可支配年收入为（折合人民币）？',
                    selectOption: [
                        '50万元以下 ', '50—100万元 ', '100—500万元 ', '500—1000万元 ', '1000万元以上 '
                    ],
                     optionValue: '',
                }, {
                    content: '在您每年的家庭可支配收入中，可用于金融投资（储蓄存款除外）的比例为',
                    selectOption: [
                        '小于10% ', '10%至25% ', '25%至50% ', '大于50% ',
                    ],
                     optionValue: '',
                }, {
                    content: '您是否有尚未清偿的数额较大的债务，如有，其性质是?',
                    selectOption: [
                        '没有 ', '有，住房抵押贷款等长期定额债务 ', '有，信用卡欠款、消费信贷等短期信用债务 ', '有，亲戚朋友借款 ',
                    ],
                     optionValue: '',
                }, {
                    content: '您的投资知识可描述为:',
                    selectOption: [
                        '有限：基本没有金融产品方面的知识 ', '一般：对金融产品及其相关风险具有基本的知识和理解', '丰富：对金融产品及其相关风险具有丰富的知识和理解 ',
                    ],
                     optionValue: '',
                }, {
                    content: '您的投资经验可描述为:',
                    selectOption: [
                        '除银行储蓄外，基本没有其他投资经验  ', '购买过债券、保险等理财产品', '参与过股票、基金等产品的交易 ', '参与过权证、期货、期权等产品的交易',
                    ],
                     optionValue: '',
                }, {
                    content: '您有多少年投资基金、股票、信托、私募证券或金融衍生产品等风险投资品的经验?',
                    selectOption: [
                        '没有经验  ', '少于2年 ', '2至5年 ', '5至10年 ', '10年以上'
                    ],
                     optionValue: '',
                }, {
                    content: '您计划的投资期限是多久？',
                    selectOption: [
                        '1年以下 ', '1至3年 ', '3至5年 ', '5年以上 ',
                    ],
                     optionValue: '',
                }],
                nowQuestion: {},
                AnswerStatus: 'start',
                // rem:null,
            }
        },
        components: {
    
        },
        created() {
            this.getDataLits();
            // this.rem = width * 100 / 750;
        },
        mounted() {},
        computed: {
    
        },
        methods: {
            //上一题，下一题
            getOtherQuestion(type) {
                if(this.nowQuestion.optionValue==''){
                    dialogConfirm('info','请先进行选择')
                    return false;
                }
                this.listArr.forEach(item => {
                    if (item.id == this.nowQuestion.id) {
                        item.optionValue = this.nowQuestion.optionValue;
                    }
                });
    
                let id = this.nowQuestion.id + type
                this.listArr.forEach(item => {
                    if (item.id == id) {
                        this.nowQuestion = item;
                    }
                });
                // if(type==1){
                //     let id = this.nowQuestion.id+1
                //     this.listArr.forEach(item => {
                //         if(item.id==id){
                //             this.nowQuestion = item;
                //         }
                //     });
                // }else if(type==-1){
                //     let id = this.nowQuestion.id-1
                //     this.listArr.forEach(item => {
                //         if(item.id==id){
                //             this.nowQuestion = item;
                //         }
                //     });
                // }
            },
            //获取所有题目
            getDataLits() {
                let number = 1;
                this.listArr.forEach(item => {
                    item.id = number;
                    number++;
                });
                this.nowQuestion = this.listArr[0];
            },
            commitAnswer(){
                this.AnswerStatus = 'success';
            }
        }
    }
</script>

<style lang="scss" scoped>
    html{
        
    }
    .content {
        position: fixed;
        top: 0px;
        left: 0px;
        bottom: 0px;
        right: 0px;
        background-color: white;
        padding: 20px;
    }
    .question-select-item {
        margin: 5px 0px;
    }
    .question-select-btn-box{
        height: 40px;
        width: 100%;
        position: absolute;
        bottom: 0px;
        left: 0px;
        right: 0px;
    }
</style>



